@import"https://fonts.googleapis.com/css2?family=Dancing+Script&family=Edu+TAS+Beginner&display=swap";body{background-color:#8fb299 !important;margin:0;padding:0;font-size:10px}@media screen and (min-width: 471px){wrapper{box-sizing:border-box;display:block;max-width:55rem;padding:1rem;margin:10% auto;background-color:#faebd7;border-radius:1.5rem;text-align:center}wrapper #text{color:#130901;font-family:"Dancing Script",cursive,sans-serif;font-size:5rem}wrapper #author{font-family:"Edu TAS Beginner",cursive,sans-serif;font-size:2.5rem;text-align:end;margin-top:1rem;padding-right:4rem}wrapper #new-quote,wrapper #tweet-quote{float:right;margin:3rem -1rem;font-family:"Dancing Script",cursive,sans-serif;font-size:2.5rem;color:#faebd7;background-color:#695f51;transition:.6s;border-radius:1.5rem;border:none;padding:1rem}wrapper #new-quote:hover,wrapper #tweet-quote:hover{background-color:#211e19;cursor:pointer}wrapper #tweet-quote{text-decoration:none;margin-right:3rem}wrapper #tweet-quote::before{content:"";font-family:FontAwesome;color:#faebd7;font-size:2.5rem;margin-right:1rem}}@media(max-width: 470px){wrapper{box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center;gap:1rem;margin:10% auto;background-color:#faebd7;text-align:center}wrapper #text{color:#130901;font-family:"Dancing Script",cursive,sans-serif;font-size:3rem;padding-top:3rem}wrapper #author{font-family:"Edu TAS Beginner",cursive,sans-serif;font-size:2rem;margin-top:1rem}wrapper #new-quote,wrapper #tweet-quote{float:right;margin:3rem -1rem;font-family:"Dancing Script",cursive,sans-serif;font-size:1.5rem;color:#faebd7;background-color:#695f51;transition:.6s;border-radius:1.5rem;border:none;padding:1rem}wrapper #new-quote:hover,wrapper #tweet-quote:hover{background-color:#211e19;cursor:pointer}wrapper #tweet-quote{text-decoration:none;margin-top:-2rem}wrapper #tweet-quote::before{content:"";font-family:FontAwesome;color:#faebd7;font-size:1.5rem;margin-right:1rem}}